@font-face {
    font-family: STKaiti;
    /*src: url('http://o70agn5fz.bkt.clouddn.com/font/STKaiti.ttf') format('truetype');*/
    src: url("/font/STKaiti.ttf");
    font-weight: normal;
    font-style: normal;
}

body {
    position: relative;
    cursor: default;
    width: 100%;
    margin: 0;
    color: white;
    height: 100%;
    font-size: 2em;
    background-color: #333;
    text-align: center;
    font-family: Baskerville, Georgia, "Liberation Serif", "Kaiti SC", STKaiti, "AR PL UKai CN", "AR PL UKai HK", "AR PL UKai TW", "AR PL UKai TW MBE", "AR PL KaitiM GB", KaiTi, KaiTi_GB2312, DFKai-SB, "TW\-Kai", serif;
    -webkit-transition: all 200ms;
    -o-transition: all 200ms;
    transition: all 200ms;
    overflow: hidden;
    overflow-scrolling: touch;
    -webkit-overflow-scrolling: touch;
    /*noinspection CssInvalidPropertyValue,CssOverwrittenProperties*/
    word-break: break-word !important;
    /*noinspection CssOverwrittenProperties*/
    word-break: break-all;
    /*word-wrap: break-word;*/
}

span, i {
    -webkit-transition: all 200ms;
    -o-transition: all 200ms;
    transition: all 200ms;
}

.container {
    /*width: 100%;*/
    /*height: 100%;*/
    /*padding: 0 1vw;*/
    overflow-x: hidden;
    overflow-y: scroll;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /*align-items: center;*/
}

.cover {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
}

h2 {
    margin: 0;
    font-size: 1.8rem;
    font-weight: bold;
    letter-spacing: -0.025em;
    color: #fff;
}

li {
    list-style: none;
}

::-webkit-scrollbar {
    width: 0;
}

.tocContainer {
    margin: 5vh 0;
}

.backButton {
    position: absolute;
}

.tocRight {
    background: -webkit-gradient(linear, left top, right top, from(rgba(51, 51, 51, 0)), color-stop(rgba(51, 51, 51, 0.9)), color-stop(rgba(51, 51, 51, 1)), to(rgba(51, 51, 51, 1)));
    background: -webkit-linear-gradient(left, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0.9), rgba(51, 51, 51, 1), rgba(51, 51, 51, 1));
    background: -o-linear-gradient(left, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0.9), rgba(51, 51, 51, 1), rgba(51, 51, 51, 1));
    background: linear-gradient(to right, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0.9), rgba(51, 51, 51, 1), rgba(51, 51, 51, 1));
    position: fixed;
    right: 0;
    top: 0;
    width: 40%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-transition: all 300ms;
    -o-transition: all 300ms;
    transition: all 300ms;
}

.bottomBar {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(51, 51, 51, 0)), color-stop(rgba(51, 51, 51, 0.9)), color-stop(rgba(51, 51, 51, 1)), to(rgba(51, 51, 51, 1)));
    background: -webkit-linear-gradient(top, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0.9), rgba(51, 51, 51, 1), rgba(51, 51, 51, 1));
    background: -o-linear-gradient(top, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0.9), rgba(51, 51, 51, 1), rgba(51, 51, 51, 1));
    background: linear-gradient(to bottom, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0.9), rgba(51, 51, 51, 1), rgba(51, 51, 51, 1));
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 52px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
}

ruby {
    font-size: 1em;
    color: #cba;
}

rt {
    font-size: 0.7em;
    color: #ddd;
}

p {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    padding: 0 12px;
    font-size: 24px;
    line-height: 1.5em;
    margin: 0;
}

span {
    display: inline-block;
}

a {
    color: #eee;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
}

a:hover {
    color: #09c;
}

::-moz-selection {
    color: #f88;
}

::selection {
    color: #f88;
}

.icon {
    padding: 0 8px;
    font-size: 18px;
}

.f {
    color: #ff77a9;
}

.f rt {
    color: #ff94c2;
}

.f ruby {
    color: #ec407a;

    /* color: #bf5f82; */
}

.m {
    color: #80d6ff;
}

.m rt {
    color: #9be7ff;
}

.m ruby {
    color: #42a5f5;
}

.g {
    color: #98ee99;
}

.g rt {
    color: #b2fab4;
}

.g ruby {
    color: #66bb6a;
}



